import "./styles.css";
import React, { Component } from "react";
import "bulma/css/bulma.min.css";

import Cevapi from "./Cevapi";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      meat: 10
    };
  }

  handleSubmit = (e) => {
    e.preventDefault();
  };

  handleChange = (e) => {
    this.setState({ meat: e.target.value });
  };

  render() {
    return (
      <div className="main">
        <h1 align="center" className="title">
          Cevapi recept
        </h1>

        <div align="center">
          <figure className="image is-128x128">
            <img
              className="is-rounded"
              src="https://cardamommagazine.com/wp-content/uploads/2019/04/cevapi-bosnian-sausages-500x500.jpg"
              width="300"
            />
          </figure>
        </div>

        <div className="box field">
          <label className="label">
            Enter how much ground beef you have in lbs?
          </label>
          <div className="control">
            <input
              className="input is-primary"
              value={this.state.meat}
              onChange={this.handleChange}
              type="text"
              placeholder="Ground beef in lbs "
            />
          </div>
        </div>

        {this.state.meat > 0 && <Cevapi meat={this.state.meat} />}
      </div>
    );
  }
}

export default App;
