import React, { Component } from "react";

import "bulma/css/bulma.min.css";

class Cevapi extends Component {
  render = () => {
    return (
      <div className="columns is-mobile box">
        <div className="column is-two-thirds">
          <div className="block">
            <h4>
              For {this.props.meat} lbs of ground beef you need the following:
            </h4>
          </div>

          <h3 className="subtitle">
            Salt: {(this.props.meat / 2) * 0.02 * 1000} g
          </h3>
          <h3 className="subtitle">
            Peper: {(this.props.meat / 2) * 0.01 * 1000} g
          </h3>
          <h3 className="subtitle">
            Soda: {(this.props.meat / 2) * 0.5 * 3.38} oz
          </h3>
          <h3 className="subtitle">Garlic: {this.props.meat} each</h3>
          <h3 className="subtitle">Onion: {this.props.meat / 5} each</h3>
        </div>
      </div>
    );
  };
}

export default Cevapi;
